/**
 * @see {@link https://bud.js.org/extensions/bud-preset-wordpress/editor-integration/filters}
 */

import * as __roots_filters from '@roots/wordpress-hmr/filters';

__roots_filters.register(
  () => import.meta.webpackContext(
    '@scripts/filters',
    {recursive: true, regExp: /.*\.filter\..*$/}
  ),
  (id, context) => {
    if (import.meta.webpackHot) return import.meta.webpackHot.accept(id, context)
  },
);;

/**
 * @see {@link https://webpack.js.org/api/hot-module-replacement/}
 */
if (import.meta.webpackHot) import.meta.webpackHot.accept(console.error);
